
































































































import { Component, Vue } from "vue-property-decorator";
import Common from '../shared/index';
@Component
export default class HamburgerMenu extends Vue {
    private open = true;
    private closeOnClick = false;
    private actionMenu =false;
    private items: Array<{title: string,icon:string}>=[
        { title: 'About',icon:'mdi-information-outline' },           
        { title: 'Services' ,icon:'mdi-book-cog-outline'}, 
        { title: 'Resume',icon:'mdi-briefcase-outline' },  
        { title: 'Skills' ,icon:'mdi-laptop'},       
        { title: 'Portfolio',icon:'mdi-apps' },
        { title: 'Contact',icon:'mdi-account' }
    ];

    private headerBg =false;    

    get colorStyle(): string{
        return this.$store.getters.getColors;
    }

    created(): void{
        this.$root.$refs.HamburgerMenu = this;
    }  
    
    navigateToSection(item: string) : void{
        Common.navigateToSection(item);
        this.actionMenu=false;      
    }
}
