












































import { Component, Vue } from "vue-property-decorator";

@Component({
  components: {},
})
export default class Banner extends Vue {   
    get colorTheme(){
        return this.$store.getters.getColorTheme;
    }   
    
    private socialLinks: any = [
        {
            title:'Linkedin',
            link:'http://linkedin.com/in/srivind',
            icon:'mdi-linkedin'
        },
        {
            title:'Github',
            link:'http://github.com/srivind',
            icon:'mdi-github'
        },
        {
            title:'Gmail',
            link:'https://mail.google.com/mail/?view=cm&fs=1&to=srivindv@gmail.com',
            icon:'mdi-gmail'
        },
        {
            title:'Instagram',
            link:'http://instagram.com/',
            icon:'mdi-instagram'
        },
        {
            title:'Facebook',
            link:'http://facebook.com/',
            icon:'mdi-facebook'
        }
    ]

    private profileImg: any = require('@/assets/photo.jpg');
    private constants: any = require('../constants/index.json');


    redirectUrl(url: string): void{
        if(url.includes('mail')){
            if (/Mobi/.test(navigator.userAgent)) {
                const mailtoLink = "mailto:" + 'srivindv@gmail.com' +
                "?subject=" + encodeURIComponent('') +
                "&body=" + encodeURIComponent('');
                window.location.href = mailtoLink;
            } else{
                window.open(`https://mail.google.com/mail/?view=cm&fs=1&to=srivindv@gmail.com`, '_blank');
            }
        }else{
            window.open(url, '_blank');
        }
    }

    downloadResume(){
        window.open(this.constants.resume, '_self');
    }

    get experienceData(){
        let today: any = new Date;
        let joinDate: any = new Date("2020-12-01");
        var diffDate: any = new Date(today - joinDate);
            
        const years = diffDate.toISOString().slice(0, 4) - 1970 
        const months = diffDate.getMonth() 

        // Convert the duration into total months
        const totalMonths = (years * 12) + months;
        
        // Calculate the total duration in years
        const totalYears = totalMonths / 12;
        
        // Determine the rounded duration
        const roundedYears = totalYears > 3.6 ? Math.ceil(totalYears) : Math.floor(totalYears);
        
        return roundedYears;
    }
    
}

