


















































import { Component, Vue } from 'vue-property-decorator';
import Title from '@/components/Title.vue';
@Component({
  components: {  
    Title
  },
})
export default class Contact extends Vue {
    get colorStyle(){
        return this.$store.getters.getColors;
    }
    private titleData: any = {
        title:"Contact Me",
        subtitle: "Feel free to contact",
        type: 'center'
    }

    private contactFormValid: any = true;

    private rules={
        nameRules: [
            (v: any) => !!v || 'Name is required',
            (v: any) => (v && v.length <= 10) || 'Name must be less than 10 characters',
        ],
        subjectRules: [
            (v: any) => !!v || 'Subject is required'
        ],
        messageRules: [
            (v: any) => !!v || 'Message is required'
        ]
    }

    private contact = {
        name:'',
        email:'srivindv@gmail.com',
        subject:'',
        message:''
    }

    contactSubmit(): void{
        if ((this.$refs.contactForm as Vue & { validate: () => boolean }).validate()) {  
            if (/Mobi/.test(navigator.userAgent)) {
                const mailtoLink = "mailto:" + this.contact.email +
                "?subject=" + encodeURIComponent(this.contact.subject + ' - ' + this.contact.name ) +
                "&body=" + encodeURIComponent(this.contact.message);
                window.location.href = mailtoLink;
            } else {
                window.open(`https://mail.google.com/mail/?view=cm&fs=1&to=${this.contact.email}&&su=${this.contact.subject} | ${this.contact.name}&body=${this.contact.message}`, '_blank');
            }           

            const form: any = this.$refs.contactForm
            form.reset();
        }
    }
}
