import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    colorStyle:{
        color:'#6851df'
    },
    colorTheme: {
        color1:'#6851df',
        color2:'#ba38d2'
    }
  },
  mutations: {
    COLOR_ASSIGN(state: any, colorStyle: any) {
      state.colorStyle.color = colorStyle.color;
    },
    COLORTHEME_ASSIGN(state: any, colorTheme: any) {
        state.colorTheme = colorTheme;
    }
  },
  actions: {
    setColor({ commit }: any, colorStyle: any) {
      commit('COLOR_ASSIGN',colorStyle);
    },
    setColorTheme({ commit }: any, colorTheme: any) {
        commit('COLORTHEME_ASSIGN',colorTheme);
    }
  },
  modules: {
  },
  getters: {
    getColors(state: any) {
      return state.colorStyle
    },
    getColorTheme(state: any) {
        return state.colorTheme
    }
  }
})
