



















































import { Component,  Vue } from "vue-property-decorator";
import Title from '@/components/Title.vue';
import Interests from '@/components/Interests.vue'; 
@Component({
  components: {
    Title,
    Interests
  }
})
export default class Intro extends Vue {

    private titleData: any ={
        title:"About Me",
        subtitle: "I can Develop Websites",
        type: 'left'
    }

    private skillSet: any=[
        {
            id:1,
            name:"Front End Development",
            value:78,
            color:'#5e62b5'
        },
        {
            id:2,
            name:"Back End Development",
            value:78,
            color:'#d15c8b'
        },
        {
            id:3,
            name:"Programming",
            value:88,
            color:'#F54768'
        },
        {
            id:4,
            name:"UI Design",
            value:84,
            color:'#FF9677'
        }
    ] 

    get experienceData(){
        let today: any = new Date;
        let joinDate: any = new Date("2020-12-01");
        var diffDate: any = new Date(today - joinDate);
        return { year : diffDate.toISOString().slice(0, 4) - 1970 ,month : diffDate.getMonth() }
    }
    
}
