











import { Component, Vue } from 'vue-property-decorator';
@Component({
  components: {    
  },
})
export default class App extends Vue{
    private loader = true;   

    mounted(): void{
        const html: any = document.getElementsByTagName('html')[0]
        html.style.overflow = 'hidden' 
        setTimeout(() => {
            this.loader = false;  
            html.style.overflow = 'auto'   
        }, 1500);

