




















































































import { Component, Vue } from 'vue-property-decorator';
import Header from '@/components/Header.vue';
import Banner from '@/components/Banner.vue';
import Intro from '@/components/Intro.vue';
import Services from '@/components/Services.vue';
import Resume from '@/components/Resume.vue';
import Skills from '@/components/Skills.vue';
import Contact from '@/components/Contact.vue';
import Portfolio from '@/components/Portfolio.vue';
import Certification from '@/components/Certification.vue';
import Footer from '@/components/Footer.vue';
import Common from '../shared/index';
@Component({
  components: {  
    Header,
    Banner,Intro,Services,Resume,Contact,Skills,Portfolio,Certification,Footer
  },
})
export default class Home extends Vue {

    private  scrollBtn = false;
    private  scrolled = 0;
    private  direction: any='top'
    private  fab: any=false;
    private  fling: any=false;
    private  hover: any=false;
    private  tabs: any=null;
    private  top: any=false;
    private  right: any=true;
    private  bottom: any=true
    private  left: any=false
    private  transition: any='slide-y-reverse-transition';
    private  colorPaletteDialog: any=false;

    private themes: any = [
        {
            color1:'#6851df',
            color2:'#ba38d2'
        },
        {
            color1:'#ff3d62',
            color2:'#ffc136'
        },
        {
            color1:'#3d62ff',
            color2:'#26c9c9'
        },
        {
            color1:'#ff3d8a',
            color2:'#3b63ff'
        },
        {
            color1:'#fa3dff',
            color2:'#3bffe6'
        },
        {
            color1:'#ffa857',
            color2:'#7e3bff'
        },
        {
            color1:'#F75672',
            color2:'#F756AA'
        },
        {
            color1:'#F3696E',
            color2:'#F8A902'
        }
    ]
    private colorThemeBtnColor = '#26c9c9';

    mounted(){
       window.addEventListener('scroll', this.getScrolledValue);  
    }
    get colorStyle(){
        return this.$store.getters.getColors;
    }

    getScrolledValue(){
        const scrolled: any = this.scrolled = Common.progressBarFunction();
        if(scrolled>5){
            this.scrollBtn=true;
        }else{
            this.scrollBtn=false;
        }
        const appBar: any= document.getElementById("progressBar");
        appBar.style.width = scrolled + "%";          
    }

    get scrolledVal(){
        return Math.round(this.scrolled);
    }
    
    scrollToTop(){
        window.scrollTo({ top: 0, behavior: 'smooth' });
    }

    openColorDialog(){
        this.colorPaletteDialog=true;
    }

    darkMode() {
        this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
    }

    setColorTheme(item: any): void{        
        document.documentElement.style.setProperty('--primary', item.color1);
        document.documentElement.style.setProperty('--secondary', item.color2);
        this.colorThemeBtnColor = item.color2;
        this.colorPaletteDialog = false;
    }       
    
}
